.popup__container{
    position: relative;
    max-width: 430px;
    width: 85%;
    background-color: #fff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    cursor: auto;
}
@media screen and (max-width: 597px){
    .popup__container{
        max-width: 282px;
    }
}